
#app,body{
  background-color: #F5F7FA;
}
.msg-text{
  text-align: center;
  color: #999999;
  line-height: 3rem;
}
.msg-box{
  width: 95%;
  margin: 0 auto;
  background-color: #FFFFFF;
  border-radius: 0.5rem;
}
.msg-box-a,.msg-box-b,.msg-box-c{
  width: 90%;
  margin: 0 auto;
}
.msg-box-a{
  line-height: 4rem;
}
.msg-box-c{
  margin-top: 1rem;
  line-height: 3rem;
  border-top: 1px solid #EEEEEE;
  overflow: hidden;
}
.msg-box-b{
  color: #999999;
  font-size: 0.9rem;
  overflow: hidden;
}
.msg-box-c-l{
  color: #5C96F6;
}
.msg-box-a-r{
  color: #999999;
  font-size: 0.9rem;
}
.msg-box-img{
  width: 100%;
  margin-top: 1rem;
}
.msg-box-a img{
  float: right;
  height: 0.8rem;
  margin-top: 1.6rem;
  margin-left: 0.5rem;
}
.msg-box-a-l {
  width: 40%;
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
}



