
#app,body{
  background-color: rgba(165, 42, 42, 0)!important;
}
img {
  border: 0px;
}
ul,
li {
  list-style-type: none;
}
.lottery-box {
  overflow: hidden;
}
.lottery-box .title {
  text-align: center;
  padding: 50px 0;
  font-size: 18px;
  color: #fff;
}
.lottery {
  -webkit-animation: changeBg 1s ease infinite;
          animation: changeBg 1s ease infinite;
  overflow: hidden;
  padding: 4vw;
  margin: 0 auto;
  background-repeat: no-repeat;
  background-size: contain;
  height: 92%;
}
@-webkit-keyframes changeBg {
0% {
    background-image: url(../assets/img/gongge_biue.png);
}
100% {
    background-image: url(../assets/img/gongge_yellow.png);
}
}
@keyframes changeBg {
0% {
    background-image: url(../assets/img/gongge_biue.png);
}
100% {
    background-image: url(../assets/img/gongge_yellow.png);
}
}
.lottery .lottery-item {
  height: 100%;
  position: relative;
}
/* 10  */
.lottery .lottery-item ul .box_sum10 {
  width: 20vw;
  position: absolute;
  padding-right: 10px;
}
.lottery .lottery-item ul .box_sum10:nth-child(2) {
  left: 25%;
}
.lottery .lottery-item ul .box_sum10:nth-child(3) {
  left: 50%;
}
.lottery .lottery-item ul .box_sum10:nth-child(4) {
  left: 75%;
}
.lottery .lottery-item ul .box_sum10:nth-child(5) {
  left: 75%;
}
.lottery .lottery-item ul .box_sum10:nth-child(5),.lottery .lottery-item ul .box_sum10:nth-child(10) {
  top: 21vw;
}
.lottery .lottery-item ul .box_sum10:nth-child(5) .box,.lottery .lottery-item ul .box_sum10:nth-child(10) .box{
  height: 38vw;
}
.lottery .lottery-item ul .box_sum10:nth-child(5) .box .gongge_img img,.lottery .lottery-item ul .box_sum10:nth-child(10) .box .gongge_img img {
  margin: 10vw auto 1vw;
}
.lottery .lottery-item ul .box_sum10:nth-child(6),.lottery .lottery-item ul .box_sum10:nth-child(7),.lottery .lottery-item ul .box_sum10:nth-child(8),.lottery .lottery-item ul .box_sum10:nth-child(9){
  top: 61vw;
}
.lottery .lottery-item ul .box_sum10:nth-child(6) {
  left: 75%;
}
.lottery .lottery-item ul .box_sum10:nth-child(7) {
  left: 50%;
}
.lottery .lottery-item ul .box_sum10:nth-child(8) {
  left: 25%;
}
.lottery .lottery-item ul .box_sum10:nth-child(9) {
  left: 0;
}
.lottery .lottery-item ul .box_sum10:nth-child(10) {
  left: 0;
}
.lottery .lottery-item ul .box_sum10 .box {
  height: 19vw;
  position: relative;
  text-align: center;
  overflow: hidden;
  background: #6757bc;
  margin: 0.5vw;
  border:1px solid #7e67fc;
  border-radius: 5px;
}
.lottery .lottery-item ul .box_sum10 .box .gongge_img img {
  display: block;
  height: 6vw;
  margin: 2vw auto 1vw;
}
.lottery .lottery-item ul .box_sum10 .box p {
  color: #FFFFFF;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  font-size: 0.6rem;
  margin: 0;
}
/* 14  */
.lottery .lottery-item ul .box_sum14 {
  width: 17vw;
  position: absolute;
  padding-right: 10px;
}
.lottery .lottery-item ul .box_sum14:nth-child(2),.lottery .lottery-item ul .box_sum14:nth-child(11) {
  left: 20%;
}
.lottery .lottery-item ul .box_sum14:nth-child(3),.lottery .lottery-item ul .box_sum14:nth-child(10) {
  left: 40%;
}
.lottery .lottery-item ul .box_sum14:nth-child(4),.lottery .lottery-item ul .box_sum14:nth-child(9) {
  left: 60%;
}
.lottery .lottery-item ul .box_sum14:nth-child(5),.lottery .lottery-item ul .box_sum14:nth-child(6),.lottery .lottery-item ul .box_sum14:nth-child(7),.lottery .lottery-item ul .box_sum14:nth-child(8) {
  left: 80%;
}
.lottery .lottery-item ul .box_sum14:nth-child(6),.lottery .lottery-item ul .box_sum14:nth-child(14) {
  top: 20vw;
}
.lottery .lottery-item ul .box_sum14:nth-child(7),.lottery .lottery-item ul .box_sum14:nth-child(13) {
  top: 40vw;
}
.lottery .lottery-item ul .box_sum14:nth-child(8),.lottery .lottery-item ul .box_sum14:nth-child(9),.lottery .lottery-item ul .box_sum14:nth-child(10),.lottery .lottery-item ul .box_sum14:nth-child(11),.lottery .lottery-item ul .box_sum14:nth-child(12){
  top: 60vw;
}
.lottery .lottery-item ul .box_sum14 .box {
  height: 19vw;
  position: relative;
  text-align: center;
  overflow: hidden;
  background: #6757bc;
  margin: 0.5vw;
  border:1px solid #7e67fc;
  border-radius: 5px;
}
.lottery .lottery-item ul .box_sum14 .box .gongge_img img {
  display: block;
  height: 6vw;
  margin: 2vw auto 1vw;
}
.lottery .lottery-item ul .box_sum14 .box p {
  color: #FFFFFF;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  font-size: 0.6rem;
  margin: 0;
}
.lottery .lottery-item ul li.on .box {
  /*background: #7e67fc;*/
  border: 1px solid #51E8F1!important;
}
.lottery .lottery-item ul li.on .box p {
  color: #fff;
}
.lottery .lottery-item .lottery-start {
  position: absolute;
  left: 21vw;
  width: 40vw;
  height: 38vw;
  top: 22vw;
}
.lottery .lottery-item .lottery-start .box {
  width: 36vw;
  font-size: 0.6rem;
  color: #fff;
  cursor: pointer;
  text-align: center;
  overflow: hidden;
  background: url(../assets/img/xingpan.png) no-repeat;
  background-size:auto 100%;
  height: 34vw;
  margin: 2vw auto;
}
.lottery .lottery-item .lottery-start .box p b {
  font-size:1rem;
  margin-top: 12vw;
  display: block;
}
.lottery .lottery-item .lottery-start .box:active {
  opacity: 0.7;
}
.lottery .lottery-item .lottery-start .box.gray {
  background: url(../assets/img/3.png) no-repeat center;
  background-size: 100% 100%;
}
.lottery .lottery-item .lottery-start .box.gray p {
  color: #708ABF;
  font-weight: bold;
}
.mask {
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.7);
  position: fixed;
  overflow: hidden;
  z-index: 222;
  top: 0;
  left: 0;
}
.lottery-alert {
  max-width: 400px;
  text-align: center;
  z-index: 10000;
  border-radius: 10px;
  background: #fff;
  padding: 20px;
  position: fixed;
  left: 0;
  right: 0;
  margin: auto;
  top: 50%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
}
.lottery-alert h1 {
  font-size: 18px;
  font-weight: bold;
  color: #D92B2F;
}
.lottery-alert img {
  display: block;
  height: 120px;
  margin: 0 auto;
}
.lottery-alert h2 {
  font-weight: normal;
  color: #D92B2F;
  font-size: 15px;
  padding-top: 15px;
}
.lottery-alert p {
  color: #666;
  font-size: 16px;
  padding-top: 5px;
}
.lottery-alert .btnsave {
  border-radius: 3px;
  -webkit-box-shadow: none;
          box-shadow: none;
  height: 40px;
  cursor: pointer;
  line-height: 40px;
  color: #fff;
  margin-top: 12px;
  background: -webkit-gradient(linear, left top, left bottom, from(rgba(213, 60, 63, 1)), to(rgba(201, 20, 24, 1)));
  background: linear-gradient(180deg, rgba(213, 60, 63, 1) 0%, rgba(201, 20, 24, 1) 100%);
  font-size: 16px;
}



