
.friend-info-top{
  width: 95%;
  margin: 0 auto;
  overflow: hidden;
}
.friend-info-top h3{
  color: #333333;
}
.friend-info-top p{
  color: #666;
}
.friend-info-bottom{
  width: 95%;
  margin: 0 auto;
}
.friend-info-table{
  width: 100%;
  border: 1px solid #666666;
  /* border-collapse: separate; */
  border-collapse: collapse;
  border-spacing: 0;
  text-align: center;
}
.friend-info-table th,.friend-info-table td{
  border: 1px solid #666666;
}
.friend-info-table td img{
  height: 1.5rem;
}



