
body,#app{
  background-color: #F5F7FA;
}
.feedback-top{
  width: 100%;
  min-height: 10rem;
  background-color: #FFFFFF;
  overflow: hidden;
  margin-top: 1rem;
  margin-bottom: 1rem;
}
.feedback-top-text{
  width: 95%;
  height: 10rem;
  margin: 0 auto;
  margin-left: 2.5%;
  padding: 0.5rem 0;
  border: none;
  resize:none;
}
.feedback-bottom{
  /* width: 100%; */
  background-color: #FFFFFF;
  padding: 1rem;
}
.feedback-tel{
  background-color: #FFFFFF;
  margin-top: 1rem;
  line-height: 3rem;
}
.feedback-tel span{
  color: #333333;
  margin-left: 2.5%;
  font-size: 1.1rem;
}
.feedback-tel input{
  width: 60%;
  float: right;
  margin-right: 2.5%;
  border:none;
  text-align: right;
}
.feedback-button{
  width: 80%;
  margin: 0 auto;
  background:-webkit-gradient(linear,right top, left top,from(rgba(90,145,246,1)),to(rgba(121,218,250,1)));
  background:linear-gradient(-90deg,rgba(90,145,246,1) 0%,rgba(121,218,250,1) 100%);
  border-radius: 3rem;
  text-align: center;
  margin-top: 3rem;
  line-height: 3rem;
  color: #FFFFFF;
}



